module MathHammer.View

open Fable.Core
open Fable.Core.JsInterop
open Fable.Helpers.React
open Fable.Helpers.React.Props
open Types


let root model dispatch =
  let top = 0.
  let bottom = 50.
  let displayModel fill stroke model posX posY = 
    circle 
      [ Cx (!^ posX); Cy (!^ posY); R (!^ "3"); Fill fill; Stroke stroke; StrokeWidth (!^ "1")  ]
      []
  let chunkBySize length (xs: seq<'T>) =
        let rec loop xs =
            [
                yield Seq.truncate length xs |> Seq.toList
                match Seq.length xs <= length with
                | false -> yield! loop (Seq.skip length xs)
                | true -> ()
            ]
        loop xs


  let distribute offsetY f models =
    let chunks = models |> chunkBySize 10
    let chunkSize = 50. / float(chunks.Length)
    chunks
    |> List.mapi (fun rowIndex m -> 
        let offsetY' = (float rowIndex * chunkSize) + offsetY
        let sum = (float m.Length * 3.)
        let centerpoint = sum / 2.0
        m |> List.fold(fun (sum',ms) m -> 
            let offsetX = (sum' + (3.0 / 2.0)) - centerpoint
            sum' + 3.0, f m offsetX offsetY'::ms) (sum,[]))
    |> List.collect snd
  let attackers models = 
    models 
    |> distribute (top + 25.) (displayModel "#79CE0B" "#396302")

  let defenders models = 
    models 
    |> distribute (bottom + 25.) (displayModel "#0B79CE" "#023963")
  svg 
      [ ViewBox "0 0 100 100"; unbox ("width", "100%") ]
      (rect 
         [ SVGAttr.Y (!^ top)
           unbox ("width", "100%")
           unbox ("height", "50%")
           Fill "#FFEEEE"] []
        :: attackers model.Attacker
      @ rect 
         [ SVGAttr.Y (!^ bottom)
           unbox ("width", "100%")
           unbox ("height", "50%")
           Fill "#EEEEFF"] []
         :: defenders model.Defender)
      



